/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p>Despite my attempt to <a href=\"http://www.papascott.de/archives/2012/08/16/travel-blog/\">start a travel blog</a> a couple of months ago, we don't actually get out and about that much. Owning a business and having a school-aged child just cramps our style somehow. So it's somewhat unusual that this month, we have not just one, but two (count 'em. two!) foreign trips planned.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2012/10/Hotel_100.jpg\" alt=\"Hotel 100\" border=\"0\" width=\"100\" height=\"75\" style=\"float:right;\" />The first is this week and actually a business trip. We all (we're taking number one son out of school for 3 days) fly to Antalya, Turkey, for the annual fall franchisee meeting at some luxury hotel built for Russian oligarchs. It will be warmer than home, though, and we are extending our stay by a night so we have all day Saturday to ourselves before flying back just in time for school and work on Monday.</p>\n<p><a href=\"http://www.papascott.de/wordpress/wp-content/uploads/2012/10/AlMdzRpCEAA3-f5.jpg\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2012/10/AlMdzRpCEAA3-f5-300x224.jpg\" alt=\"\" title=\"AlMdzRpCEAA3-f5.jpg\" width=\"300\" height=\"224\" class=\"alignright size-medium wp-image-4844\" /></a>The second half of October our son as fall break from school, and I'll be cashing in <a href=\"http://twitter.com/papascott/statuses/167510377884684288\">my birthday present</a> from my family, a 5 night trip to London. Of course, \"birthday present\" means 1) the entire family comes along, and 2) they only come up with the idea and I get to do all the planning myself. Despite having lived in Europe for over 20 years, we've never been to London so it will be all new to us. All I know about London is that it's wonderful and expensive, so we'll be staying in Greenwich and be taking the train a lot to see the sights.</p>\n<p>After that we'll be all travelled out for while.</p>","fields":{"title":"Antalya and London","date":"08 Oct 2012","url":"https://papascott.de/2012-10-08-antalya-and-london/"}}},"pathContext":{"slug":"/2012-10-08-antalya-and-london/","prev":{"fields":{"title":"They are all here for your anniversary, Miss Sophie","date":"06 Sep 2012","slug":"/2012-09-06-they-are-all-here-for-your-anniversary-miss-sophie/"}},"next":{"fields":{"title":"Touch 'Em All","date":"25 Oct 2012","slug":"/2012-10-25-touch-em-all/"}}}}

/***/ })

